import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';

// @mui
import { styled, alpha, useTheme } from '@mui/material/styles';
import {
  Toolbar,
  OutlinedInput,
  InputAdornment,
  Typography,
  Tooltip,
  IconButton,
  Box,
  Stack
} from '@mui/material';
// component

import FormatAlignJustifyIcon from '@mui/icons-material/FormatAlignJustify';
import GridViewIcon from '@mui/icons-material/GridView';
import { IconSearch, IconTrash } from '@tabler/icons';
// ----------------------------------------------------------------------

const StyledRoot = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3)
}));

const StyledSearch = styled(OutlinedInput)(({ theme }) => ({
  width: 240,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': {
    width: 320,
    boxShadow: theme.shadows[6]
  },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${alpha(theme.palette.grey[500], 0.32)} !important`
  }
}));

// ----------------------------------------------------------------------

CustomerListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func
};

export default function CustomerListToolbar({
  numSelected,
  filterName,
  onFilterName
}) {
  const theme = useTheme();
  const navigate = useNavigate();
  return (
    <StyledRoot
      sx={{
        ...(numSelected > 0 && {
          color: 'text.default',
          bgcolor: 'background.default'
        })
      }}
    >
      <Box sx={{ flexGrow: 1 }}>
        {numSelected > 0 ? (
          <Stack
            direction={'row'}
            spacing={2}
            justifyContent={'flex-start'}
            alignItems={'center'}
          >
            <Typography component="div" variant="subtitle1">
              {numSelected} selected
            </Typography>
            <Tooltip title="Delete">
              <IconButton>
                <IconTrash />
              </IconButton>
            </Tooltip>
          </Stack>
        ) : (
          <StyledSearch
            value={filterName}
            onChange={onFilterName}
            placeholder="Search user..."
            startAdornment={
              <InputAdornment position="start">
                <IconSearch
                  size={20} // set custom `width` and `height`
                  color={theme.palette.grey[400]} // set `stroke` color
                  stroke={2} // set `stroke-width`
                  strokeLinejoin="miter" // override other SVG props
                />
              </InputAdornment>
            }
          />
        )}
      </Box>
      <Tooltip title="Delete">
        <IconButton onClick={() => navigate('/pages/customers')}>
          <FormatAlignJustifyIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Delete">
        <IconButton onClick={() => navigate('/pages/customers/card')}>
          <GridViewIcon />
        </IconButton>
      </Tooltip>
      {/* {numSelected > 0 ? (
        <></>
      ) : (
        <Tooltip title="Filter list">
          <IconButton>
             <IconFilter size={20} color={theme.palette.grey[400]}   stroke={2}   strokeLinejoin="miter" />
          </IconButton>
        </Tooltip >
      )
      } */}
    </StyledRoot>
  );
}
